import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  array_person = [{name: 'Dustin', age: 12}, {name: 'Nelson', age:32}, {name: 'David', age:33}];
  title = 'welcom app component';
  removePersonByName(name){
  	const index = this.array_person.findIndex(e => e.name === name);
  	this.array_person.splice(index, 1);
  }
}
